.checkout-section {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #c0c0c0;
  padding: 30px;
}
.checkout-section .checkout-form {
  width: 60%;
  margin: 20px auto;
}
.checkout-section .checkout-form > div {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}
.checkout-section .checkout-form > div input {
  height: 40px;
  padding: 5px;
}
.checkout-section .checkout-form > div button {
  height: 40px;
  margin: 0;
}

.order-section {
  display: flex;
  flex-direction: column;
  border: 1px solid #c0c0c0;
  margin: 20px;
}
.order-section .order-alert {
  background-color: green;
  padding: 10px;
  color: #fff;
  font-size: 22px;
  text-transform: capitalize;
  margin-bottom: 5px;
  text-align: center;
}
.order-section .order-content {
  width: 60%;
  margin: 10px auto;
}
.order-section .order-content table {
  width: 100%;
  border: 1px solid #c0c0c0;
  margin-bottom: 20px;
  border-radius: 5px;
  padding: 20px;
}
.order-section .order-content table th {
  background-color: #2a5ea5;
  color: #fff;
  padding: 10px;
}
.order-section .order-content table tbody {
  padding: 5px;
}
.order-section .order-content table tbody tr {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #c0c0c0;
  padding: 0 10px 5px;
  margin: 20px 0;
}
.order-section .order-content table tbody .total-price {
  font-size: 24px;
  border: 3px solid #7a7a7a;
  text-transform: uppercase;
  padding: 10px;
}